import React from 'react';
import styled from "styled-components";

const TopSectionContainer = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(141, 149, 177, 0.2);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 13%;
  z-index: 99;
`
const Logo = styled.h1`
  margin: 0;
  color: #fff;
  font-weight: 800;
  font-size: 80px;
`
const Slogan = styled.h4`
  margin: 0;
  color: #fff;
  font-weight: 700;
  font-size: 30px;
  margin-top: 10px;
`
const Paragraph = styled.p`
  margin: 0;
  margin-top: 3em;
  color: #fff;
  font-size: 14px;
  line-height: 1.5;
  font-weight: 500;
  max-width: 30%;
  text-align: center;
`
const DonateButton = styled.button`
  outline: none;
  border: none;
  background-color: #27b927;
  color:#fff;
  font-size:16px;
  font-weight: 700;
  border-radius: 8px;
  padding:8px 2em;
  margin-top:3em;
  cursor:pointer;
  border:2px solid transparent;
  transition: all 350ms ease-in-out;
  
  &:hover{
   background-color: transparent;
    border:2px solid #27b927
  }
  
  
`


function TopSection() {
    return (
        <TopSectionContainer>
            <Logo>Global Warming</Logo>
            <Slogan>Keep it cool for safe living</Slogan>
            <Paragraph>
                You can help us cool off our world and have it go back to it's best
                state ever by donating to help fix our only world and our beloved EARTH!
                Be cool and let the earth be cool. Let the ice burgs to live. Globe is
                warming and will set to fire. Stop polluting, it will cost extra.
            </Paragraph>
            <DonateButton>Donate</DonateButton>
        </TopSectionContainer>
    );
}

export default TopSection;